import { useState } from "react";
import "../ContactForm/ContactForm.css";
import { addContact } from "../../redux/actions";
import { getContacts } from "../../redux/selectors";
import { useSelector, useDispatch } from "react-redux";
import shortid from "shortid";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

function ContactForm() {
  const [state, setState] = useState({ name: "", number: "" });
  const contacts = useSelector(getContacts);
  const dispatch = useDispatch();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setState((prev) => ({ ...prev, [name]: value }));
  };

  const checkRepeatName = (name) => {
    return contacts.find(
      (contact) => contact.name.toLowerCase() === name.toLowerCase()
    );
  };

  const checkRepeatNumber = (number) => {
    return contacts.find((contact) => contact.number === number);
  };

  const handleSubmite = (e) => {
    e.preventDefault();
    // onSubmite(state);
    if (checkRepeatName(state.name)) {
      toast.info(`'${state.name}' is already in use!`, {
        autoClose: 2500,
      });
    } else if (checkRepeatNumber(state.number)) {
      toast.info(`ðŸ¤” ${state.number} is already in use`, {
        autoClose: 2500,
      });
    } else {
      dispatch(addContact({ ...state, id: shortid.generate() }));
      setState({ name: "", number: "" });
    }
  };

  return (
    <form id="contact" onSubmit={handleSubmite}>
      <label>
        <p>Name:</p>
        <input
          className="input-field"
          type="text"
          name="name"
          pattern="^[a-zA-ZÐ°-ÑÐ-Ð¯]+(([' -][a-zA-ZÐ°-ÑÐ-Ð¯ ])?[a-zA-ZÐ°-ÑÐ-Ð¯]*)*$"
          title="Ð˜Ð¼Ñ Ð¼Ð¾Ð¶ÐµÑ‚ ÑÐ¾ÑÑ‚Ð¾ÑÑ‚ÑŒ Ñ‚Ð¾Ð»ÑŒÐºÐ¾ Ð¸Ð· Ð±ÑƒÐºÐ², Ð°Ð¿Ð¾ÑÑ‚Ñ€Ð¾Ñ„Ð°, Ñ‚Ð¸Ñ€Ðµ Ð¸ Ð¿Ñ€Ð¾Ð±ÐµÐ»Ð¾Ð². ÐÐ°Ð¿Ñ€Ð¸Ð¼ÐµÑ€ Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan Ð¸ Ñ‚. Ð¿."
          required
          value={state.name}
          onChange={handleChange}
        />
      </label>
      <label>
        <p>Number:</p>
        <input
          className="input-field"
          type="tel"
          name="number"
          pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
          title="ÐÐ¾Ð¼ÐµÑ€ Ñ‚ÐµÐ»ÐµÑ„Ð¾Ð½Ð° Ð´Ð¾Ð»Ð¶ÐµÐ½ ÑÐ¾ÑÑ‚Ð¾ÑÑ‚ÑŒ Ñ†Ð¸Ñ„Ñ€ Ð¸ Ð¼Ð¾Ð¶ÐµÑ‚ ÑÐ¾Ð´ÐµÑ€Ð¶Ð°Ñ‚ÑŒ Ð¿Ñ€Ð¾Ð±ÐµÐ»Ñ‹, Ñ‚Ð¸Ñ€Ðµ, ÐºÑ€ÑƒÐ³Ð»Ñ‹Ðµ ÑÐºÐ¾Ð±ÐºÐ¸ Ð¸ Ð¼Ð¾Ð¶ÐµÑ‚ Ð½Ð°Ñ‡Ð¸Ð½Ð°Ñ‚ÑŒÑÑ Ñ +"
          required
          value={state.number}
          onChange={handleChange}
        />
      </label>
      <button type="submit" className="submit-button">
        Add contact
      </button>
      <ToastContainer />
    </form>
  );
}

export default ContactForm;
